* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inputWrapper {
  width: 192px;
  overflow: hidden;
  margin: 50px auto;
}

.input {
  border: none;
  border-bottom: 1px solid #a1afbf;
  border-radius: 0px;
  box-sizing: border-box;
  color: #0b1f35;
  font-size: 32px;
  font-weight: 500;
  height: 41px;
  margin: 0 8px;
  outline: none;
  text-align: center;
  width: 32px;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 8px;
}

.activeInput {
  border-color: #292929;
}
